<template>
	<view class="content">
		<view class="title">手机号登陆/注册</view>
		<view class="uni-form-item uni-column">
			<input type="tel" class="uni-input" name="" placeholder="请输入手机号" />
		</view>
		<view class="uni-form-item uni-column column-with-btn" v-if="message">
			<input type="number" class="uni-input" name="" placeholder="短信验证码" />
			<button :class="{active : !disableCodeBtn}" :disabled="disableCodeBtn" @tap="sendCode">{{codeBtn.text}}</button>
		</view>
		<view class="uni-form-item uni-column" v-else>
			<input type="password" class="uni-input" name="" placeholder="请输入密码" />
		</view>
		<button type="primary">登陆/注册</button>
		<view class="links" @click="onPassword">{{message?'密码':'短信'}}登陆</view>
		<view class="weixin">
			<view class="weixinimg">
				<image src="../../static/weixin.png" mode=""></image>
			</view>
			<view class="text">
				微信登陆
			</view>
		</view>
		<view class="rule">
				<radio style="transform:scale(0.7)" value="sada"  :checked="actil" color="#45CFFE" @click="clickRad"/><text>人才库<text style="color: #45CFFE;">《用户协议》</text>和<text style="color: #45CFFE;">《个人信息保护政策》</text></text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				actil:false,
				short:'密码',
				message:true,
				captchaImg: '',
				seconds: 10,
				codeBtn: {
					text: '获取验证码',
					waitingCode: false,
					count: this.seconds
				}
			}
		},
		onLoad() {

		},
		methods: {
			sendCode: function () {
				this.codeBtn.waitingCode = true;
				this.codeBtn.count = this.seconds;
				this.codeBtn.text = this.codeBtn.count + 's';
				
				let countdown = setInterval( () => {
					this.codeBtn.count--;
					this.codeBtn.text = this.codeBtn.count + 's';
					if( this.codeBtn.count < 0 ){
						clearInterval(countdown);
						this.codeBtn.text = '重新发送';
						this.codeBtn.waitingCode = false;
					}
				},1000);
			},
			gotoLogin: function () {
				uni.navigateTo({
					url: 'login'
				})
			},
			onPassword(){
				this.message=!this.message;
				
			},
			clickRad(){
				this.actil=!this.actil
			}
		},
		computed: {
			disableCodeBtn: function (){
				return this.codeBtn.waitingCode || this.captchaImg.length < 4;
			} 
		}
	}
</script>

<style lang="scss" scoped>
	.rule{
		font-size: 24rpx;
	}
	.weixin{
		margin: 0rpx auto;
		margin-top: 200rpx;
		width: 150rpx;
		height: 200rpx;
		text-align: center;
		.weixinimg{
			margin: 0 auto;
			width: 70rpx;
			height: 70rpx;
			border-radius: 50%;
			image{
				width: 100%;
				height: 100%;
				border-radius: 50%;
			}
		}
		.text{
			font-size: 30rpx;
			margin-top: 20rpx;
		}
	}
	$color-primary: #36bca8;
	.content{
		padding: 60upx 50upx 100upx;
	}
	.title{
	    font-size: 50rpx;
		font-weight: 700;
		margin-bottom: 100rpx;
	}
	.uni-form-item{
		margin-bottom: 40upx;
		padding: 0;
		border-bottom: 1px solid #e3e3e3;
		.uni-input{
			font-size: 30upx;
			padding: 7px 0;
		}
	}
	.column-with-btn{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		button{
			font-size: 24upx;
			margin: 0;
			width: 180upx;
			text-align: center;
			&:after{
				border: none
			}
			&.active{
				background-color: $color-primary;
				color: $uni-text-color-inverse;
			}
		}
	}
	.img-captcha{
		width: 150upx;
		height: 60upx;
	}
	button[type="primary"]{
		background-color: $color-primary;
		border-radius: 0;
		font-size: 34upx;
		margin-top: 60upx;
	}
	.links{
		text-align: right;
		margin-top: 40upx;
		font-size: 26upx;
		color: #999;
		
	}
</style>
